import {useState,useEffect} from "react";
// id ulid  import
import { ulid } from "ulid";
// src/apis/todos.js export
// import useTodo.js
// getAllTodosData, addTodoData, deleteTodoData, updateTodoData 
//  todoDataimport
import * as todoData from "../apis/todos"

// useTodo
// export
export const useTodo = () => {
    // todolistTODO
    // setTodoList todoList
    // todoList 
    const [todoList,setTodoList] = useState([]);

    useEffect(() => {
        // useEffect() 
        // 
        // TODO getAllTodosData() 
        todoData.getAllTodosData().then((todo) => {
            // TODO
            // TODO
            // Array.reverse()[ES2015]
            // 
            // 
            // todoList
            setTodoList([...todo].reverse());
        });
    },[]);

    // todoListItem  done(/)
    // 
    const toggleTodoListItemStatus = (id,done) => {
        // find()  true 
        // 
        // done(/) todoListItem  id 
        //  todoItem 
        const todoItem = todoList.find((item) => item.id === id);

        //  todoList  todoItem
        // done(/)
        const newTodoItem = {...todoItem, done: !done};

        // updateTodoData()  id TODO
        //  todoList 
        todoData.updateTodoData(id,newTodoItem).then((updateTodo) => {
            const newTodoList = todoList.map((item) => 
                // idtodoList  item 
                //  done(/) updateTodo 
                //  newTodoList 
                item.id !== updateTodo.id ? item : updateTodo
            );

            // todoList(state) newTodoList
            setTodoList(newTodoList);
        });
    };

    // TODO
    const addTodoListItem = (todoContent) => {
        const newTodoItem = {
            // content TODO
            content: todoContent,
            // idulid
            id: ulid(),
            // TODO
            done: false
        };

        // addTodoData() TODO
        //  todoList 
        // addTodoData() TODO
        return todoData.addTodoData(newTodoItem).then((addTodo) => {
            // todoList (state) newTodoItem 
            setTodoList([addTodo,...todoList])
        });
    };

    // TODO
    const deleteTodoListItem = (id) => {
        // todoDatatodoList
        // deleteTodoData()idTODO
        // todoList
        // deleteTodoData()idTODO
        todoData.deleteTodoData(id).then((deleteListItemId) => {
            const newTodoList = todoList.filter(
                // TODO id TODO
                // 
                // id TODO
                (item) => item.id !== deleteListItemId
            );
            // todoList (state)
            // todoList (state)  id TODO
            // 
            setTodoList(newTodoList);
        });
    };

    // 
    return {
        todoList,
        toggleTodoListItemStatus,
        addTodoListItem,
        deleteTodoListItem
    };
};
